/* index-content */
.main-content {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-color: white;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    gap: 64px;
    /* padding: 64px 32px; */
}

/* default button */
.button {
    /* background-color: var(--button-color); */
    border: 2px solid var(--color-base);
    color: var(--color-base);
    font-weight: 700;
    padding: 20px;
    padding-inline: 30px;
    border-radius: 5px;

    /* box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25); */
    /* margin: 30px;
 */
    text-align: center;
    font-size: 22px;
}

.button:hover {
    background-color: var(--button-color);
}

/* default container */
.container {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 18px;
    background-color: #ffffff;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    padding: 64px;

    flex-direction: column;
    gap: 32px;
}

.container-column {
    display: flex;
    flex-direction: column;

    align-items: stretch;
    gap: 64px;
    padding: 64px;
}

@media (min-width: 768px) {
    .container {
        flex-direction: row;
    }
    .container-column {
        align-items: flex-start;
    }
}

.container-centered {
    align-items: center;
}

.main-site-gallery {
    gap: 0;
    display: flex;
    flex-direction: row;
}
@media (max-width: 768px) {
    .main-site-gallery {
        flex-direction: column;
    }
}

.main-site-img {
    width: 50%;
}

@media (max-width: 768px) {
    .main-site-img {
        width: 100%;
    }
}

.icon {
    width: 60px;
}

.project-image {
    flex: 1 1 calc(33.33% - 20px); /* Każdy obrazek zajmuje około 1/3 szerokości wiersza */
    max-width: 100%;
    height: 50vh;
    object-fit: cover;
    transition: transform 1000ms;
}
.projects-container {
    display: flex;
    gap: 5px;
    flex-wrap: wrap;

    justify-content: center;
}

@media (max-width: 768px) {
    .project-image {
        flex: 1 1 50%; /* Na tabletach dwa obrazy na wiersz */
    }
}

@media (max-width: 480px) {
    .project-image {
        flex: 1 1 100%; /* Na telefonach jeden obraz na wiersz */
    }
}

.photo-container:is(:hover, :focus) figure::after {
    opacity: 1;
}

figure:is(:hover, :focus) figcaption {
    opacity: 1;
    transform: translate3d(0, 0, 0);
}

.project-title {
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    place-items: center;
    text-align: center;
    width: 100%;
    height: 100%;
    color: #ffff;
    z-index: 1;
    opacity: 0;
    transition: opacity 600ms, transform 600ms;
}

.project-container::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    width: 200%;
    height: 200%;
    background: rgba(0, 0, 0, 0.5);
    transform-origin: center;
    display: flex;
    opacity: 0;
    transform: scale(2);
    transition: opacity 300ms;
}

.project-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0;
    position: relative;
    overflow: hidden;
}

/* tag */
.title {
    font-size: 42px;
    font-style: normal;
    color: var(--color-base);
    font-weight: 700;
}

.nav-current {
    font-weight: 700;
}
/* header */
.site-cover {
    width: 100%;
    height: 50vw;
}
.site-header-cover {
    width: 100%;
    height: 30vw;
    margin-top: 100px;
    z-index: 1;
}

.logo {
    margin: 5px 20px;
    max-height: 80px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.header-navigation {
    position: absolute;
    display: flex;
    align-items: center;
}

@media (min-width: 784px) {
    .header-navigation {
        align-items: center;
    }
}

.cover-container {
    z-index: 1;

    display: flex;
    align-items: center;
    margin-bottom: 20px;
}

/* companies */
.company-list {
    display: flex;
    flex-direction: row;
    max-width: max-content;
    flex-wrap: wrap;
    justify-content: center;
    gap: 10px;
    margin-bottom: 20px;
}
.company-image {
    border-radius: 20%;
    min-height: 8vw;
    min-width: 8vw;
    max-width: 8vh;
    max-height: 8vh;
    margin: 10px;
    object-fit: contain;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
}

/* projects */
.projects-content {
    display: flex;
    flex-direction: column;
    align-items: center;
}

/* about */
.name-surname {
    font-size: 58px;
    color: var(--color-bg);
    font-weight: 700;
    text-wrap: nowrap;

    margin-bottom: 20px;
}

.about-container {
    display: flex;
    align-items: center;
    text-align: center;

    flex-direction: row;
}
.about-card {
    display: flex;
    flex-direction: row;
    align-items: center;
    text-align: center;
}
.right-card-side {
    display: flex;
    flex-direction: column;
    align-items: start;
}

.about-content {
    font-size: 24px;
    line-height: 2;
    text-align: start;
    text-wrap: wrap;
}
